import animation from './modules/animation';
import promTimer from './modules/promTimer';
import modal from './modules/modal';
import filter from './modules/filter';
import calculator from './modules/calculator';

import {parseRequestURL} from './helpers/utils';

import {CalcPage} from './views/calc-page.component';
import {HomePageComponent} from './views/home-page.component';
import {Error404} from './views/error404';


const Routes = {
    '/': HomePageComponent,
    '/select-coffee': CalcPage,
    // '/coffee-page': CoffeePage,
};

function router() {
    // const calcPage = new CalcPage('calc-page');
    const homePage = new HomePageComponent('home');
    // const error404 = new Error404();

    const  home = document.getElementById('home');
       
    
    // homePage.render().then(html  => home.innerHTML = html);

    const request = parseRequestURL(),
        parsedURL = `/${request.resource || ''}`,
        page = Routes[parsedURL] ? new Routes[parsedURL]() : new Error404();

    page.render().then((html) => {
        home.innerHTML = html;
        page.afterRender();
    });

    

}

window.addEventListener('load', router);
window.addEventListener('hashchange', router);

window.addEventListener('DOMContentLoaded', () => {
    animation();
    promTimer();
    modal('[data-modal]', '.modal');
    filter();
    calculator();
});

